<template>
    <div class="main">
        <aside-com></aside-com>

        <div class="main-container scroll">
            <header-com></header-com>
            <div class="main-container-cover">
                <div class="main-breadcrumb">
                    <!-- 面包屑 -->
                    <a-breadcrumb class="main-breadcrumb-cover">
                        <a-breadcrumb-item v-for="(item, index) in breadcrumbList" :key="index">
                            <router-link :to="{ name: item.name, query: item.query }">{{ item.meta.name }}</router-link>
                        </a-breadcrumb-item>
                    </a-breadcrumb>
                </div>
                <router-view></router-view>
            </div>

            <div class="footer">Copyright © 2018 舞环科技</div>
        </div>
    </div>
</template>
<script>
import AsideCom from "@/components/common/aside.component";
import HeaderCom from "@/components/common/header.component";
export default {
    name: "Main",
    data() {
        return {
            breadcrumbList: [],
            isCollapse: false
        };
    },
    components: {
        AsideCom,
        HeaderCom
    },
    watch: {
        $route(data) {
            this.processBread(data);
        }
    },
    methods: {
        /**
         * 处理全局面包屑
         * @param route 当前路由信息
         */
        processBread(route) {
            this.breadcrumbList = route.matched.filter(el => {
                return el.name;
            });

            this.breadcrumbList.forEach(el => {
                if (el.name === route.name) {
                    el.query = route.query;
                }
            });
        }
    },
    mounted() {
        this.processBread(this.$route);
    }
};
</script>
