<template>
    <div class="aside">
        <div class="logo" :class="!collapsed ? 'pl24' : 'pl16'">
            <img src="../../assets/logo.png" alt="" v-if="!collapsed" />
            <!-- <span v-if="!collapsed" class="ml8 mt8"></span> -->
        </div>
        <a-menu
            mode="inline"
            theme="dark"
            @click="select"
            :openKeys="openKeys"
            @openChange="openChange"
            v-model="selectedKeys"
            :inlineCollapsed="collapsed"
        >
            <template v-for="item in routes">
                <!-- 第一次循环中需要展开 -->
                <a-sub-menu :key="item.name" v-if="item.meta.unfold">
                    <span slot="title">
                        <span
                            class="iconfont aside-icon"
                            :class="item.meta.iconType"
                            v-if="item.meta.iconType"
                        ></span>
                        <a-icon type="close" v-else />
                        <span>{{ item.meta.name }}</span>
                    </span>
                    <template v-for="ele in item.children">
                        <!-- 第二次循环中需要展开 -->
                        <a-sub-menu :key="ele.name" v-if="ele.meta.unfold">
                            <span slot="title">
                                <a-icon :type="ele.meta.iconType" />
                                <span>{{ ele.meta.name }}</span>
                            </span>
                            <!-- 第三次循环 -->

                            <template v-for="child in ele.children">
                                <a-sub-menu
                                    :key="child.name"
                                    v-if="child.meta.unfold"
                                >
                                    <span slot="title">
                                        <a-icon :type="child.meta.iconType" />
                                        <span>
                                            {{ child.meta.name }}
                                        </span>
                                    </span>
                                    <a-menu-item
                                        v-for="element in child.children"
                                        :key="element.name"
                                    >
                                        <a-icon :type="element.meta.iconType" />
                                        <span>{{ element.meta.name }}</span>
                                    </a-menu-item>
                                </a-sub-menu>
                                <a-menu-item v-else :key="child.name">
                                    <a-icon :type="child.meta.iconType" />
                                    <span>{{ child.meta.name }}</span>
                                </a-menu-item>
                            </template>
                        </a-sub-menu>
                        <!-- 第二次循环中不需要展开 -->
                        <a-menu-item
                            :key="ele.name"
                            v-if="!ele.meta.unfold && ele.meta.hide"
                        >
                            <!-- <a-icon :type="ele.meta.iconType" /> -->

                            <!-- <span>{{ele.name }}</span> -->
                            <span>{{ ele.meta.name }}</span>
                        </a-menu-item>
                    </template>
                </a-sub-menu>
                <!-- 第一次循环中不需要展开 -->
                <a-menu-item :key="item.name" v-else>
                    <!-- <a-icon :type="item.meta.iconType" /> -->
                    <span
                        class="iconfont aside-icon"
                        :class="item.meta.iconType"
                        v-if="item.meta.iconType"
                    ></span>
                    <a-icon type="close" v-else />
                    <span>{{ item.meta.name }}</span>
                </a-menu-item>
            </template>
        </a-menu>
    </div>
</template>

<script>
// import { Icon } from 'ant-design-vue';
// const IconFont = Icon.createFromIconfontCN({
//   scriptUrl: '//at.alicdn.com/t/font_950712_yolw5a2kfi.css',
// })
export default {
    data() {
        return {
            collapsed: false,
            routes: [],
            selectedKeys: [],
            active: "",
            openKeys: [],
            role: [],
            flag: false
        };
    },
    methods: {
        select(obj) {
            if(this.$route.name !== obj.key){
                this.$router.push({
                    name: obj.key
                });
            }
            
        },
        // 设置当前菜单选中
        setActive() {
            // 获取 active name值
            let opt = this.$route.matched[0];
            if (opt.meta.unfold) {
                this.$set(this, "openKeys", [this.$route.matched[0].name]);
                this.active = this.$route.name;
            } else {
                this.active = opt.name;
            }
            this.selectedKeys = [this.$route.matched[1].name];
        },
        openChange(openKeys) {
            const latestOpenKey = openKeys.find(
                key => this.openKeys.indexOf(key) === -1
            );
            let arr = latestOpenKey ? [latestOpenKey] : [];
            this.$set(this, "openKeys", arr);
        },
        updateRole() {
           
            // this.flag = true;
            const routes = this.$router.options.routes;
            
            this.routes = routes.filter(el => {
                console.log(el.meta,777);
                return el.meta.hide;
            });
        }
    },
    components: {
        // IconFont,
    },
    watch: {
        $route: {
            handler: function(val, oldVal) {
                // this.selectedKeys = [val.name];
                this.setActive();
            },
            // 深度观察监听
            deep: true
        }
    },
    mounted() {
        this.$eventBus.on("updateRole", () => {
            this.updateRole();
        });
        this.$eventBus.on("updateCollapse", data => {
            this.collapsed = data.status;
        });
    },
    created() {
        this.setActive();
        this.updateRole();
    }
};
</script>

