<template>
    <div>
        <div class="header">
            <div class="header-collapse">
                <a-icon @click="toggleCollapsed" :type="collapsed ? 'menu-unfold' : 'menu-fold'" style="font-size:20px;cursor: pointer;">
                </a-icon>
            </div>
            <div class="header-right">
                <!-- <a-icon type="search" class="icon"></a-icon>
                <a-icon type="bell" class="icon"></a-icon> -->
                <div class="header-user">
                    <div class="header-user-portrait">
                        <div class="logo">
                            <img :src="$domain+avatar" alt="">
                        </div>
                        <div class="name"> {{userName}}</div>
                    </div>
                    <ul class="header-user-signout">
                        <li @click="loginOut">退出登录</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>

</template>

<script type="text/javascript">
export default {
    name: "topHeader",
    data() {
        return {
            userName: "kind",
            avatar:'',
            isCollapse: false,
            changePassDialog: false,
            oldPassword: "",
            newPassword: "",
            surePassword: "",
            changePassword: {},
            collapsed: false,
            baseUrl:process.env.VUE_APP_API_URL
        };
    },
    created() {
        // this.$axios.get('/api/user/info').then(data=>{
        //     this.user=data.data
        // })
        if(sessionStorage.userInfo){
            let userInfo= JSON.parse(sessionStorage.userInfo);
            this.userName = userInfo.username;
            this.avatar = userInfo.avatar;
        }
    },
    methods: {
        loginOut() {
            this.$http.post("/admin/user/logout").then(data => {
                this.$router.push({ name: "login" });
                localStorage.clear();
            });
        },
        // 更新侧边栏展开和关闭状态
        collapse() {
            let isCollapse = !this.isCollapse;
            this.$eventBus.emit("updateCollapse", {
                status: isCollapse
            });
        },
        changePass() {
            this.changePassDialog = true;
        },
        beforeClose() {
            this.changePassDialog = false;
        },
        toggleCollapsed() {
            this.collapsed = !this.collapsed;
            this.$eventBus.emit("updateCollapse", {
                status: this.collapsed
            });
        }
    },
    mounted() {
        this.$eventBus.on("updateCollapseCb", data => {
            this.isCollapse = data.status;
        });
    }
};
</script>

